export const MODAL_OPEN_CLASS = 'active';
export const MODAL_ACTIVE_CLASS = 'modal--active';
export const BODY_NO_SCROLL_CLASS = 'no-scroll';

export default class ModalComponent {
  static closeModal(selector) {
    const modals = document.querySelectorAll(selector);

    if (modals.length > 0) {
      for (let i = 0; i < modals.length; i++) {
        modals[i].classList.remove(MODAL_OPEN_CLASS);
        modals[i].classList.remove(MODAL_ACTIVE_CLASS);
      }

      document.body.classList.remove(BODY_NO_SCROLL_CLASS);
    }
  }

  static openModal(selector) {
    const modals = document.querySelectorAll(selector);

    if (modals.length > 0) {
      for (let i = 0; i < modals.length; i++) {
        modals[i].classList.add(MODAL_OPEN_CLASS);
        modals[i].classList.add(MODAL_ACTIVE_CLASS);
      }

      document.body.classList.add(BODY_NO_SCROLL_CLASS);
    }
  }
}



